import React from 'react'
import './Programs.css'
import { programsData } from '../../data/programsData'    // array of data 
import RightArrow from '../../assets/rightArrow.png'

export const Programs = () => {
  return (
    <div className="programs" id="programs">
      
      {/* header */}
      <div className="programs-header">
        <span>EXPLORE OUR</span>
        <span className='stroke-text'>PROGRAMS</span>
        <span>TO SHAPE YOU</span>
      </div>

      {/* fetch data from programsData file */}
      <div className="programs-categories">
        {programsData.map((program)=>(            // for every program fetch following
          <div className="category">  
            {program.image}                      
            <span>{program.heading}</span>
            <span>{program.details}</span>

            <div className="join-now">
              <span>Join Now</span>
              <img src={RightArrow} alt="" />
            </div>
          </div>
        ))}
      </div>

    </div>
  )
}
