import React from 'react'
import { Header } from '../Header/Header'
import './Hero.css'

import her_image from '../../assets/hero_image.png'
import her_image_back from '../../assets/hero_image_back.png'
import heart from '../../assets/heart.png'
import calories from '../../assets/calories.png'

import { motion } from 'framer-motion'
import NumberCounter from 'number-counter'

export const Hero = () => {
    
    const transition = {type: 'spring', duration: 3}
  
    return (
    <div className="hero">
        <div className="left-h">
            <Header />
        
            {/* best ad */}
            <div className="the-best-ad">
                <motion.div
                initial={{left: '238px'}}
                whileInView={{left: '8px'}}
                transition={transition}
                >
                </motion.div>
                <span>the best fitness club in the town</span>
            </div>

            {/* hero text */}
            <div className="hero-text">
                <div>
                    <span className='stroke-text'>Shape </span>
                    <span>Your</span>
                </div>
                <div>
                    <span>Ideal Body</span>
                </div>
                <div>
                    <span>In here we will help you to shape and build your ideal body and live up your life to fullest</span>
                </div>
            </div>

            {/* figures */}
            <div className="figures">
                <div>
                    <span>
                        <NumberCounter start={100} end={140} delay={4} preFix="+"/>
                    </span>
                    <span>EXPERT COACHES</span>
                </div>
                <div>
                    <span>
                        <NumberCounter start={800} end={978} delay={4} preFix="+"/>
                    </span>
                    <span>MEMBERS JOINED</span>
                </div>
                <div>
                    <span>
                        <NumberCounter start={20} end={50} delay={4} preFix="+" />
                    </span>
                    <span>FITNESS PROGRAMS</span>
                </div>
            </div>

            {/* hero buttons */}
            <div className="hero-buttons">
                <button className="btn">Get Started</button>
                <button className="btn">Learn More</button>
            </div>

        </div>
        

        <div className="right-h">
            <button className='btn'>Join Now</button>
        
            <motion.div 
                initial={{right: '-1rem'}}
                whileInView={{right: '4rem'}}
                transition={transition}
                className="heart-rate"
            >
                <img src={heart} alt="" />
                <span>Heart Rate</span>
                <span>116 bpm</span>
            </motion.div>

            {/* hero images */}
            <img src={her_image} alt="" className="hero-image" />
            <motion.img 
            initial={{right: '11rem'}}
            whileInView={{right: '20rem'}}
            transition={transition}
            src={her_image_back} alt="" className="hero-image-back" />
        
            {/* caloried */}
            <motion.div
                initial={{right: '37rem'}}
                whileInView={{right: '28rem'}}
                transition={transition} 
                className="calories"
            >
                <img src={calories} alt="" />
                <div>
                    <span>Calories Burned</span>
                    <span>220 kcal</span>
                </div>
            </motion.div>
        </div>
    </div>
  )
}
